<template>
    <div class="container">
        <div id="wavin">
            <ul>
                <li v-for="job in jobs">{{ job.name}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
export default {
    name:"Job",
    data(){
        return {
            jobs:[
                {
                    name:"hahah"
                }
            ]
        }
    },
    mounted(){
        $.ajax({
            url: "/jobList",
            dataType: "application/json; charset=utf-8",
            data: {
                job_category_id_list: ["6704215862557018372"],
                keyword: "",
                limit: 10,
                location_code_list:["CT_125"],
                offset: 20,
                portal_entrance: 1,
                portal_type: 2,
                recruitment_id_list:[],
                subject_id_list: []
            },
            method: "POST",
            success: function(data){
                console.log(data);
            }
        })
    }
}
</script>

<style scoped>
.container{
    width: auto;
    height: auto;
    background-color: #fff;
}

#wavin{
    padding-top: 40px;
    background-color: #fff;
    height: 100%;
}

#wavin>ul{

}

#wavin>ul>li{
    display: inline-block;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 10px;
}

#wavin>ul>li>a{
}

#wavin>ul>li:hover,#wavin>ul>li>a:hover{
    background-color: red;
    color: white;
}

</style>


