<template>
    <div class="container">
        <canvas id="text" width="800" height="200" style="border:0px solid #d3d3d3;"></canvas>
        <div id="wavin">
            <ul>
                <li class="menus" v-for="item in pages"><a :href="item.url">{{item.title}}</a></li>
                <li class="wechat" id="wechat" @mouseover="onWechatEvent($event, 'wechat');">
                    微信
                    <div v-show="onWechat" class="wechat-img">
                        <img src="../assets/wechat.jpg"/>
                    </div>
                </li>

                <li class="wechat" id="wechatmp" @mouseover="onWechatEvent($event, 'wechat-mp');">
                    微信公众号
                    <div  v-show="onWechatMp" class="wechat-img">
                        <img src="../assets/qr_code_wavin.jpg"/>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import $ from 'jquery'
export default {
    name:"Index",
    data(){
        return {
            msg: "Sorry! this page is not opened yet!, please visit: ",
            show: "Welcome to Wavin Don's Creative Center!",
            pages:[
                {
                    title: "我的主页",
                    url: "https://www.wdcc.top"
                },
                {
                    title: "我的Github",
                    url: "https://github.com/Tangwego"
                },
                {
                    title: "我的Gitee",
                    url: "https://gitee.com/Tangwego"
                },
                {
                    title: "我的CSDN",
                    url: "https://blog.csdn.net/qq_35699215"
                },
                {
                    title: "我的简历(中文)",
                    url: "https://cv.wdcc.top/#/zh_cn"
                },
                {
                    title: "My CV(English)",
                    url: "https://cv.wdcc.top/#/en_us"
                },
            ],
            colors:[
                "#9966ff",
                "#6666ff",
                "#6699ff",
                "#9966ff",
                "#6666ff",
                "#6699ff",
                "#9966ff",
                "#6666ff",
                "#6699ff",
                "#9966ff",
                "#6666ff"
            ],
            onWechat: false,
            onWechatMp: false
        }
    },
    mounted(){
        let c = document.getElementById("text");
        let ctx = c.getContext("2d");

        ctx.font="40px 'Axure Handwriting'";
        // 创建渐变
        var gradient = ctx.createLinearGradient(0,0,c.width,0);
        for (let i = 0; i <= 10; i++) {
            gradient.addColorStop(i/10,  this.colors[i]);
        }
        // 用渐变填色
        ctx.fillStyle=gradient;
        ctx.fillText(this.show,40,100);
    },
    beforeCreate(){
        document.title = this.$route.meta.title
        window.onpopstate = function(event) {
            alert("location: " + document.location.href + ", state: " + JSON.stringify(event.state));//拿到history.state对象值
        };
        if (!location.hostname) {
             return;
        }
        if (location.hostname.startsWith("github")) {
            window.location.href = "https://github.com/Tangwego";
        } else if (location.hostname.startsWith("gitee")) {
            window.location.href = "https://gitee.com/Tangwego";
        } else if (location.hostname.startsWith("csdn")) {
            window.location.href = "https://blog.csdn.net/qq_35699215";
        } else if (location.hostname.startsWith("cv")) {
            window.location.href = "https://www.wdcc.top/#/zh_cn";
        } else {
            window.location.href = "http://119.3.109.25/"
            return;
        }
    },
    methods: {
        onWechatEvent: function(e, s){
            console.log(e,s);
            if (s == 'wechat') {
                if (!this.onWechat) {
                    this.onWechat = true;
                    this.onWechatMp = false;
                } else {
                    this.onWechat = false;
                    this.onWechatMp = false;
                }
            } else {
                if (!this.onWechatMp) {
                    this.onWechat = false;
                    this.onWechatMp = true;
                } else {
                    this.onWechat = false;
                    this.onWechatMp = false;
                }
            }
        }
    }
}
</script>

<style scoped>
.container{
    width: auto;
    height: auto;
    background-color: #fff;
}

#text{
    padding-top: 50px;
    /* background-color: #000; */
}

#wavin{
    background-color: #fff;
    height: 100%;
    font-family: 'Axure Handwriting';
}

#wavin>ul{

}

#wavin>ul>li{
    display: inline-block;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 10px;
}

#wavin>ul>li>a{
}

#wavin>ul>.menus:hover,#wavin>ul>.menus>a:hover{
    background-color: red;
    color: white;
}

#wavin>ul>.wechat:hover,#wavin>ul>.wechat>a:hover{
    cursor: pointer;
}

.wechat-img{
    position: absolute;
    width: 200px;
    height: 200px;
}

</style>


